<template>
  <div class="overlay">
    <div class="form-wrapper">
      <a href="http://piknik.cc" target="_blank">
        <v-img
          class="mb-8"
          style="margin: 0 auto"
          max-width="100px"
          src="../assets/piknik-logo-white.svg"
          alt="Piknik"
        />
      </a>
      <h2 class="white--text mb-2">{{ $t("forgotPasswordTitle") }}</h2>
      <p class="white--text mb-8">{{ $t("forgotPasswordDescription") }}</p>
      <v-form ref="form" v-model="valid">
        <v-text-field
          dark
          v-model="email"
          :rules="[rules.required, rules.email]"
          :label="$t('email')"
          type="email"
          rounded
          dense
          filled
        ></v-text-field>
        <v-btn
          v-on:click="forgotPassword"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >{{ $t("recoverPassword") }}</v-btn
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <!-- <v-btn text small rounded @click="reload()" color="white"
        >Ok</v-btn
      > -->
    </v-snackbar>
  </div>
</template>

<script>
import userService from "../services/user.service";

export default {
  data() {
    return {
      drawer: null,
      valid: true,
      rules: {
        required: (value) => !!value || this.$t("requiredField"),
        email: (v) => /.+@.+\..+/.test(v) || this.$t("emailMustBeCorrect"),
      },
      email: "",
      snackbar: false,
      snackbarText: this.$t("ifAccountExistsWeHaveSentRecoveryEmail"),
      timeout: 0,
    };
  },
  methods: {
    async forgotPassword() {
      const resp = await userService.forgotPassword(this.email);
      this.snackbar = true;
    },
    // reload() {
    //   this.email = "";
    //   this.snackbar = false;
    // },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 350px;
  height: auto;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>