<template>
  <div class="overlay">
    <div class="form-wrapper">
      <a href="http://piknik.cc" target="_blank">
        <v-img
          class="mb-8"
          style="margin: 0 auto"
          max-width="100px"
          src="../assets/piknik-logo-white.svg"
          alt="Piknik"
        />
      </a>
      <h2 class="white--text mb-2">{{ $t("registerTitle") }}</h2>
      <p class="white--text mb-8">
        {{ $t("registerDescription") }}
      </p>
      <v-form ref="form" v-model="valid">
        <v-stepper v-model="step" elevation="0" dark>
          <v-stepper-header elevation="0" class="d-none d-sm-flex">
            <v-stepper-step editable :complete="step > 1" step="1">
              {{ $t("enterEmail") }}
            </v-stepper-step>
            <v-divider></v-divider>
            <v-stepper-step :complete="step > 2" step="2">
              {{ $t("createStrongPassword") }}
            </v-stepper-step>
          </v-stepper-header>

          <v-stepper-content step="1">
            <v-text-field
              dark
              v-model="email"
              :rules="[rules.required, rules.email]"
              :label="$t('email')"
              type="email"
              :append-icon="isEmailValid ? 'mdi-arrow-right-circle' : ''"
              @click:append="isEmailValid ? submitEmail(email) : (email = '')"
              rounded
              dense
              filled
            >
              <!-- <template v-slot:append>
                  <v-btn icon color="white">
                    <v-icon>mdi-arrow-right-circle</v-icon>
                  </v-btn>
                </template> -->
            </v-text-field>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-text-field
              class="d-sm-none"
              dark
              v-model="email"
              :label="$t('email')"
              type="email"
              append-icon="mdi-pencil"
              @click:append="step = 1"
              rounded
              dense
              filled
              readonly
            >
            </v-text-field>
            <v-text-field
              dark
              v-model="password1"
              :rules="[rules.required, rules.min, rules.max, rules.password]"
              :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="show1 = !show1"
              :type="show1 ? 'text' : 'password'"
              :label="$t('password')"
              rounded
              dense
              filled
            ></v-text-field>
            <v-text-field
              dark
              v-model="password2"
              :rules="[
                rules.required,
                rules.min,
                rules.max,
                rules.password,
                checkSamePasswords,
              ]"
              :append-icon="show2 ? 'mdi-eye' : 'mdi-eye-off'"
              @click:append="show2 = !show2"
              :type="show2 ? 'text' : 'password'"
              :label="$t('password')"
              rounded
              dense
              filled
            ></v-text-field>
          </v-stepper-content>
        </v-stepper>
        <v-btn
          v-if="step == 1"
          v-on:click="submitEmail(email)"
          dark
          color="white"
          depressed
          rounded
          :disabled="!isEmailValid"
          >{{ $t("continue") }}</v-btn
        >
        <v-btn
          v-else
          v-on:click="register"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >{{ $t("register") }}</v-btn
        >
        <router-link
          class="white--text ml-2"
          style="text-decoration: none"
          to="/login"
          >{{ $t("alreadyHaveAnAccount") }}</router-link
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <v-btn text small rounded @click="reload()" color="white">{{
        $t("tryAgain")
      }}</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  props: {
    source: String,
  },
  data() {
    return {
      drawer: null,
      step: 1,
      valid: true,
      rules: {
        required: (value) => !!value || this.$t("requiredField"),
        min: (v) => v.length >= 8 || this.$t("minChars", { chars: 8 }),
        max: (v) => v.length <= 48 || this.$t("maxChars", { chars: 48 }),
        email: (v) => /.+@.+\..+/.test(v) || this.$t("emailMustBeCorrect"),
        password: (v) =>
          /^(?=.*([A-Z]){1,})(?=.*[0-9]{1,})(?=.*[a-z]{1,}).{8,48}$/.test(v) ||
          this.$t("passwordRegex"),
      },
      email: "",
      show1: false,
      password1: "",
      show2: false,
      password2: "",
      snackbar: false,
      snackbarText: this.$t("errorDuringRegistration"),
      timeout: 0,
    };
  },
  created() {
    const email = this.$route.query.email;
    if (/.+@.+\..+/.test(email)) {
      this.email = email;
      this.submitEmail(this.email);
    }
  },
  computed: {
    isEmailValid: function () {
      return /.+@.+\..+/.test(this.email);
    },
    checkSamePasswords: function () {
      return (
        this.password1 === this.password2 || this.$t("passwordsDoNotMatch")
      );
    },
  },
  methods: {
    submitEmail: function (email) {
      this.step++;

      const formData = new FormData();
      const params = new URLSearchParams(window.location.search);

      formData.append("email", email);
      formData.append("refferrer", params.get("refferrer"));
      formData.append("campaignName", params.get("campaignName"));
      formData.append("adsetName", params.get("adsetName"));
      formData.append("adName", params.get("adName"));

      const request = new XMLHttpRequest();
      request.open(
        "POST",
        "https://script.google.com/macros/s/AKfycbzujzRk7acMcYoiszJHrvReSwIWB9ZRPfbV3jCIDbTeix29ToXXtoDVvyK3TPyp-D6LTw/exec"
      );
      request.send(formData);

      gtag("event", "sign_up_email_submit");
      fbq("trackCustom", "SignUpEmailSubmit");
    },
    register: function () {
      const params = new URLSearchParams(window.location.search);
      const data = {
        email: this.email,
        password: this.password1,
        referrer: params.get("referrer") || "piknik",
      };

      this.$store
        .dispatch("register", data)
        .then(() => {
          // this.$gtag.event("sign_up");
          gtag("event", "sign_up");
          fbq("trackCustom", "SignUp");
          fbq("track", "CompleteRegistration");

          this.$router.push("/create");
        })
        .catch((err) => {
          console.log(err);
          this.snackbar = true;
        });
    },
    reload() {
      this.step = 1;
      this.email = "";
      this.password1 = "";
      this.password2 = "";
      this.snackbar = false;
    },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 450px;
  height: auto;
  margin: 0 auto;
}

.form-wrapper .v-stepper {
  background: none;
}

.form-wrapper .v-stepper .v-stepper__header {
  box-shadow: none;
}

.form-wrapper .v-stepper .v-stepper__content {
  padding-top: 16px;
  padding-bottom: 8px;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
  .form-wrapper .v-stepper .v-stepper__content {
    padding-top: 0;
    padding-bottom: 0;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>