 <template>
  <v-container fluid class="pt-0">
    <v-row>
      <v-col cols="12">
        <v-text-field
          v-model="onlineOrderingUrl"
          :label="$t('onlineOrderingUrl')"
          placeholder="https://demo.piknik.cc/"
          rounded
          filled
          hide-details
        ></v-text-field>
      </v-col>
      <v-col cols="12">
        <v-combobox
          v-model="tableIds"
          :label="$t('tableIds')"
          multiple
          small-chips
          rounded
          filled
          :hint="$t('pressEnterToCreateNewOne')"
          append-icon
          persistent-hint
        ></v-combobox>
      </v-col>
      <v-col cols="12" class="pt-0">
        <v-btn
          color="success"
          text
          rounded
          :loading="loading"
          :disabled="tableIds.length === 0 || !onlineOrderingUrl"
          @click="generatePdfFile(tableIds)"
          >{{ $t("generateQrCodes") }}</v-btn
        >
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { QRCodeCanvas } from "@cheprasov/qrcode";
import { jsPDF } from "jspdf";

const PKNK = `data:image/svg+xml;base64, `;
const grid = `data:image/png;base64,`;
const poppins = require("../assets/Poppins-Light.ttf");

export default {
  data() {
    return {
      loading: false,
      onlineOrderingUrl: null,
      tableIds: [],
    };
  },
  methods: {
    async generateQrCode(
      onlineOrderingUrl,
      restaurantId,
      deliveryMethod,
      tableId
    ) {
      const config = {
        level: "M",
        padding: 0,
        image: {
          source: PKNK,
          width: "25%",
          height: "7,9%",
          x: "center",
          y: "center",
        },
      };

      const qrCanvas = new QRCodeCanvas(
        `${onlineOrderingUrl}?restaurantId=${restaurantId}&deliveryMethod=${deliveryMethod}&tableId=${tableId}`,
        config
      );

      return await qrCanvas.toDataUrl();
    },
    async generatePdfFile(tableIds) {
      this.loading = true;

      let doc = new jsPDF({ format: [595.28, 841.89] });

      doc.addFont(poppins, "Poppins", "light");
      doc.setFont("Poppins", "light");
      doc.setFontSize(16);

      doc.addImage(grid, "JPEG", 0, 0, 595.28, 841.89);

      let rowIndex = 0;
      let columnIndex = 0;
      let count = 0;

      for (let i = 0; i < tableIds.length; i++) {
        const table = tableIds[i];
        let imageX = null;
        let imageY = null;
        let textX = null;
        let textY = null;

        switch (rowIndex) {
          case 0:
            imageY = 93;
            textY = 222 + 8;
            break;
          case 1:
            imageY = 266;
            textY = 395 + 8;
            break;
          case 2:
            imageY = 439;
            textY = 568 + 8;
            break;
          case 3:
            imageY = 612;
            textY = 741 + 8;
            break;
        }

        switch (columnIndex) {
          case 0:
            imageX = 78;
            textX = 78;
            break;
          case 1:
            imageX = 235;
            textX = 235;
            break;
          case 2:
            imageX = 392;
            textX = 392;
            break;
        }

        const qr = await this.generateQrCode(
          this.onlineOrderingUrl,
          localStorage.restaurantId,
          "inplace",
          table
        );

        doc.addImage(qr, "JPEG", imageX, imageY, 125, 125);
        doc.text(`${this.$t("tableNumber")} ${table}`, textX, textY);

        if (columnIndex !== 2) columnIndex++;
        else {
          columnIndex = 0;
          if (rowIndex !== 3) rowIndex++;
          else {
            doc.addPage([595.28, 841.89]);
            doc.addImage(grid, "JPEG", 0, 0, 595.28, 841.89);
            rowIndex = 0;
          }
        }
      }

      doc.save("QRs.pdf");
      this.loading = false;
    },
  },
};
</script>