<template>
  <div class="overlay">
    <div class="form-wrapper">
      <a href="http://piknik.cc" target="_blank">
        <v-img
          class="mb-8"
          style="margin: 0 auto"
          max-width="100px"
          src="../assets/piknik-logo-white.svg"
          alt="Piknik"
        />
      </a>
      <h2 class="white--text mb-2">{{ $t("logInTitle") }}</h2>
      <p class="white--text mb-8">
        {{ $t("logInDescription") }}
      </p>
      <v-form ref="form" v-model="valid">
        <v-text-field
          dark
          v-model="email"
          :rules="[rules.required, rules.email]"
          :label="$t('email')"
          type="email"
          rounded
          dense
          filled
        ></v-text-field>
        <v-text-field
          dark
          v-model="password"
          :rules="[rules.required, rules.min, rules.max, rules.password]"
          :append-icon="show ? 'mdi-eye' : 'mdi-eye-off'"
          @click:append="show = !show"
          :type="show ? 'text' : 'password'"
          :label="$t('password')"
          rounded
          dense
          filled
        ></v-text-field>
        <v-btn
          v-on:click="login"
          dark
          color="white"
          depressed
          rounded
          :disabled="!valid || snackbar"
          >{{ $t("logIn") }}</v-btn
        >
        <router-link
          class="white--text ml-2"
          style="text-decoration: none"
          to="/forgotpassword"
          >{{ $t("forgotPassword") }}</router-link
        >
      </v-form>
    </div>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ snackbarText }}
      <v-btn text small rounded @click="reload()" color="white">{{
        $t("tryAgain")
      }}</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
export default {
  props: {
    source: String,
  },
  data() {
    return {
      drawer: null,
      valid: true,
      rules: {
        required: (value) => !!value || this.$t("requiredField"),
        min: (v) => v.length >= 8 || this.$t("minChars", { chars: 8 }),
        max: (v) => v.length <= 48 || this.$t("maxChars", { chars: 48 }),
        email: (v) => /.+@.+\..+/.test(v) || this.$t("emailMustBeCorrect"),
        password: (v) =>
          /^(?=.*([A-Z]){1,})(?=.*[0-9]{1,})(?=.*[a-z]{1,}).{8,48}$/.test(v) ||
          this.$t("passwordRegex"),
      },
      email: "",
      show: false,
      password: "",
      snackbar: false,
      snackbarText: this.$t("wrongEmailOrPassword"),
      timeout: 0,
    };
  },
  methods: {
    login: function () {
      let data = {
        email: this.email,
        password: this.password,
      };
      this.$store
        .dispatch("login", data)
        .then(() => {
          // this.$gtag.event("login");
          gtag("event", "login");

          this.$router.push("/");
        })
        .catch((err) => {
          console.log(err);
          this.snackbar = true;
        });
    },
    reload() {
      this.email = "";
      this.password = "";
      this.snackbar = false;
    },
  },
};
</script>

<style scoped>
.overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  background: #007bff;
  z-index: 10;
  display: flex;
  align-items: center;
  text-align: center;
}

.form-wrapper {
  width: 350px;
  height: auto;
  margin: 0 auto;
}

@media (max-width: 600px) {
  .form-wrapper {
    padding: 32px;
  }
}

/deep/ .v-input__slot {
  background: rgba(255, 255, 255, 0.2) !important;
}

/deep/ .v-btn {
  color: #007bff !important;
}

/deep/ .v-application .error--text {
  color: black !important;
  caret-color: black !important;
}
</style>